import React,{useState,useEffect} from 'react'
import { useParams } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';
import rehypeHighlight from 'rehype-highlight';
import 'highlight.js/styles/atom-one-dark.css';  // Możesz wybrać inny styl z highlight.js

const Rozwiazanie = () => {
    const { klasyfikacja, rok, examId } = useParams();
    const [content, setContent] = useState('');

    const url = `https://raw.githubusercontent.com/PumaYT22/EgzaminyZawodoweINF/main/Egzaminy/${klasyfikacja}/${rok}/${examId}/rozwiazanie/rozwiazanie.md`;
    const btnurl = `https://github.com/PumaYT22/EgzaminyZawodoweINF/tree/main/Egzaminy/${klasyfikacja}/${rok}/${examId}/rozwiazanie/plikizrozwiazaniem`

    useEffect(() => {
        fetch(url)
            .then(response => response.text())
            .then(data => setContent(data))
            .catch(error => console.error('Error fetching the markdown file:', error));
    }, [url]);

    return (
        <div style={{ padding: '20px', maxWidth: '900px', margin: 'auto',textAlign:"left" }}>
        <a href={btnurl} target='_blank' rel="noreferrer"><button className='btn btn-primary'>Pobierz rozwiązanie - Github</button></a>
        <ReactMarkdown rehypePlugins={[rehypeHighlight]}>
            {content}
        </ReactMarkdown>
    </div>
    );
}

export default Rozwiazanie